.learning-home-section-1 {
    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: var(--tc-system-green-3);
        z-index: -1;
        border-radius: 0 !important;
    }
    .banner-girl {
        &::before {
            content: '';
            position: absolute;
            bottom: 0;
            left: 0;
            width: 805px;
            height: 805px;
            border-radius: 805px;
            background: #003930;
            z-index: -1;
        }
    }
}
.learning-home-section-3,
.learning-home-section-6,
.learning-home-section-7 {
    background: #f3f3e5;
    margin-top: -20px;
}
.section-portfolio-2.accordion {
    justify-content: center;
    align-content: center;
    position: relative;
    top: 0;
    left: 5vw;
    transition: all 200ms ease-in-out 50ms;
    min-height: 50vh;
    width: 90vw;
    opacity: 0;
    .accordion-item {
        margin-left: 15px;
        margin-right: 15px;
        border-radius: 10px !important;
        border: 1px solid var(--tc-system-stroke);
        background: none;
        cursor: pointer;
        display: flex;
        .accordion-link {
            background-color: var(--tc-system-white);
            transition: all 0.2s linear;
            .accordion-link-item {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: start;
                gap: 12px;
                .icon {
                    position: relative;
                    width: 44px;
                    height: 44px;
                    border-radius: 10px;
                    border: 1px solid var(--tc-system-stroke);
                    background: var(--tc-system-white);
                    transition-duration: 0.3s;

                    svg {
                        height: 14px;
                        width: 14px;
                        fill: var(--tc-theme-primary);
                    }
                    &:hover {
                        background: var(--tc-theme-primary);
                        border: 1px solid var(--tc-theme-primary);

                        svg {
                            fill: var(--tc-system-green-3);
                            transition-duration: 0.3s;
                        }
                    }
                }
            }
        }
        .accordion-img {
            height: 100%;
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;
            position: relative;
            &::before {
                content: '';
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                width: 100%;
                height: 100%;
                background: url(../imgs/sections/learning/page-home/home-section-5/logo-bg.png);
                background-position: center;
                background-size: cover;
                background-repeat: no-repeat;
            }
        }
    }
    .default {
        height: 360px;
        width: 300px;
        overflow: hidden;
        .accordion-link {
            width: 0;
            .accordion-link-item {
                height: 0;
                opacity: 0;
            }
        }
        .accordion-img {
            width: 100%;
        }
    }
    .expand {
        height: 360px;
        width: 630px;
        .accordion-link {
            width: 50%;
            padding: 0 40px;
            .accordion-link-item {
                transition: all 0.5s ease-in-out;

                height: 100%;
                opacity: 1;
            }
        }
        .accordion-img {
            width: 50%;
        }
    }
    @media (max-width: 991px) {
        .default {
            height: 230px;
            width: 100%;
            overflow: hidden;
            .accordion-link {
                width: 0;
                .accordion-link-item {
                    height: 0;
                    opacity: 0;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
        .expand {
            height: 430px;
            width: 764px;
            .accordion-link {
                width: 50%;
                padding: 0 40px;
                .accordion-link-item {
                    transition: all 0.5s ease-in-out;

                    height: 100%;
                    opacity: 1;
                }
            }
            .accordion-img {
                width: 50%;
            }
        }
    }
    @media (max-width: 767.98px) {
        .default {
            height: 150px;
            width: 100%;
            overflow: hidden;
            .accordion-link {
                width: 0;
                .accordion-link-item {
                    height: 0;
                    opacity: 0;
                }
            }
            .accordion-img {
                width: 100%;
            }
        }
        .expand {
            height: 430px;
            width: 100%;
            .accordion-link {
                width: 50%;
                padding: 0 20px;
                .accordion-link-item {
                    transition: all 0.5s ease-in-out;

                    height: 100%;
                    opacity: 1;
                }
            }
            .accordion-img {
                width: 50%;
            }
        }
    }
}
.learning-home-section-7 {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;
    &::after {
        position: absolute;
        content: '';
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        width: 100%;
        height: 100%;
        z-index: 1;
        background: rgba(213, 213, 43, 0.84);
    }
}
.section-footer-11 {
    margin-top: -20px;
    .line-border {
        width: 1px;
        height: 300px;
        background: linear-gradient(180deg, #d5d52b 0%, #01473c 100%);
    }
}
